@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Montserrat:wght@400;600;700&display=swap);

* {
 box-sizing: border-box;
 padding: 0;
 margin: 0;
}

:root {
 --header-height: 3.5rem;
 --first-color: #D9AE94;
 --title-color: hsl(0, 0%, 95%);
 --text-color: hsl(0, 0%, 75%);
 --body-color: #f0efeb;
 --body-font: "Montserrat", sans-serif;
 --second-font: "Dancing Script", cursive;
 --h2-font-size: 1.25rem;
 --small-font-size: 0.813rem;
 --font-semi-bold: 600;
 --z-tooltip: 10;
 --z-fixed: 100;
}

body {
 background-color: var(--body-color);
 color: var(--text-color);
 font-family: var(--body-font);
}

ul {
 list-style: none;
}

a {
 text-decoration: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Navbar.css */

.header {
    position: fixed; /* Fixes the navbar at the top */
    top: 0;
    width: 100%;
    height: 80px; /* Set height of your navbar */
    background-color: #797d62; /* Background color for visibility */
    z-index: 1000; /* Ensure it's on top of other elements */
    padding: 0 20px;
  }
  
  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: var(--header-height);
    margin: 1rem;
  }
  
  .nav__logo {
    color: var(--first-color);
    transition: color 0.4s;
    font-size: var(--h2-font-size);
    font-family: var(--second-font);
  }
  
  .nav__toggle,
  .nav__close {
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
  }
  
  .nav__cta {
    background-color: var(--first-color);
    color: var(--title-color);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    padding: 0.75rem 1.5rem;
  }
  
  @media screen and (max-width: 1150px) {
    .nav__menu {
      position: fixed;
      top: 0;
      right: -100%;
      background-color: hsla(0, 0%, 6%, 0.2);
      backdrop-filter: blur(16px);
      -webkit-backdrop-filter: blur(16px);
      width: 80%;
      height: 100%;
      padding: 6rem 3rem 0;
      transition: right 0.4s;
    }
  }
  @media screen and (max-width: 300px) {
    .nav__menu {
      padding: 4rem 1rem 2rem;
    }
  
    .nav__list {
      grid-row-gap: 1rem;
      row-gap: 1rem;
    }
  
    .nav__link,
    .nav__cta {
      font-size: var(--smaller-font-size);
    }
  
    .nav__cta {
      padding: 8px 16px;
      font-size: 12px;
      margin-top: 1rem;
      width: auto;
      white-space: nowrap;
    }
  }
  
  .nav__list {
    display: flex;
    flex-direction: column;
    grid-row-gap: 2.5rem;
    row-gap: 2.5rem;
  }
  
  .nav__link {
    color: var(--title-color);
    font-weight: var(--font-semi-bold);
    transition: color 0.4s;
  }
  
  .nav__link:hover {
    color: var(--first-color);
  }
  
  .nav__close {
    position: absolute;
    top: 1rem;
    right: 1.5rem;
  }
  
  .show-menu {
    right: 0;
  }
  .nav__listt {
    display: flex;
    flex-direction: row;
    grid-column-gap: 2.5rem;
    column-gap: 2.5rem;
  }
  
  .nav__menu_mobile {
    position: fixed;
    top: 0;
    right: -100%;
    background-color: hsla(0, 0%, 6%, 0.2);
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
    width: 80%;
    height: 100%;
    padding: 6rem 3rem 0;
    transition: right 0.4s;
  }
  @media screen and (min-width: 1150px) {
    .nav {
      height: calc(var(--header-height) + 1rem);
    }
    .nav__toggle,
    .nav__close {
      display: none;
    }
  
    .nav__list {
      flex-direction: row;
      grid-column-gap: 2.5rem;
      column-gap: 2.5rem;
    } 

    @media (max-width: 768px) {
        .header {
          height: 60px; /* Adjust navbar height for smaller screens */
          padding: 0 10px;
        }
    }      
  }
/* Main container for layout */
.main {
  padding-top: 90px; /* Dynamic space, same as navbar height */
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column; /* Stack the title and content vertically */
  justify-content: center;
  align-items: center;
}

/* Centered title with the updated font size and color */
.title {
  font-size: 2.5rem; /* Adjust text size for better readability */
  color: #baa587; /* Ensure contrast against the background */
  display: flex;
  justify-content: center;
  margin-bottom: 30px; /* Add space between title and content */
}

/* Content container for image and text */
.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 800px; /* Limit width for better control */
  width: 100%;
  grid-gap: 20px;
  gap: 20px; /* Space between image and text */
}

/* Body text next to the image */
.body {
  font-size: 1.5rem; /* Adjust text size for readability */
  color: #A5A58D; /* Ensure contrast against the background */
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 400px; /* Limit the width of text section */
}

/* Image with fixed size */
.image {
  width: 200px;
  height: 300px;
  object-fit: cover; /* Ensure the image maintains aspect ratio */
  justify-content: left;
}

/* Responsive behavior for smaller screens */
@media (max-width: 768px) {
  .main {
    padding-top: 90px; /* Adjust padding for smaller screens */
    font-size: 1.2rem; /* Slightly smaller text for readability */
  }

  .content {
    flex-direction: column; /* Stack image and text vertically on smaller screens */
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
  }

  .image {
    width: 100%; /* Allow image to scale for smaller screens */
    height: auto; /* Maintain aspect ratio */
  }

  .body {
    font-size: 1.2rem; /* Adjust text size for smaller screens */
    max-width: 100%; /* Allow the text section to use full width */
  }
}

